body { 
	background: #f8f8f8; 
	font-family: sans-serif; 
	font-weight: 300;
	text-align: center; 
}
p { font-size: 1.2em; }

.navbar-default { border-color: #f8f8f8; }
.top { padding-top: 4em; background-color: white;}
.first_section { padding-top: 4em; }
.section { padding: 2em; }
.main { background: white; }
.main-image img { padding: 1em; background: white;}
.center { text-align: center; }
.padding-top { margin-top: 3em; }
#story { margin-top: 3em; }

h1{
  color: #888;
  font-weight: 100;
  font-size: 40px;
}
h2, h3, h4, h5 { color: #888; font-weight: 100; }


#clockdiv{
	font-family: sans-serif;
	display: inline-block;
	font-weight: 100;
}

#clockdiv > div{
	padding: 10px;
	border-radius: 3px;
	display: inline-block;
}

#clockdiv div > span{

	color: black;
}

.smalltext{
	font-size: 1em;
}